body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient( #08fa05, #090108)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0px 20vw 0px 20vw;
  flex-direction: column;
  font-size: 1rem;
  font-family: 'Averia Serif Libre', cursive;
}

.display {
  text-align: center;
  width: 100%;
  border: solid 5px black;
  border-radius: 10px;
  min-height: 20px;
  font: inherit;
}

.button {
  margin: 5px 40px;
  height: 50px;
  border-radius: 10px;
  font: inherit;
}

.input {
  margin: 12px 30px;
  height: 40px;
  border-radius: 10px;
  font: inherit;
}

@media screen and (max-width: 800px) {
  .button {
    display: block;
    width: 80%;
    margin: 12px auto;
  }
  .input {
    display: block;
    width: 80%;
    margin: 12px auto;
  }
  .input-wrapper {
    width: 100%;
  }
}